import { Component, OnInit } from '@angular/core';
import { NgFlashMessageService } from 'ng-flash-messages';
import {AuthService} from '../services/auth.service';
import {Router} from "@angular/router"

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  email : string;
  password : string;
  constructor(private Flashmsg:NgFlashMessageService,
    private authservices:AuthService,
    private router:Router) { }

  ngOnInit() {
  }
  onLoginSubmit() {
    const user = {
      email : this.email,
      password : this.password

    }
    this.authservices.authenticateUser(user).subscribe(data => {
      if(data){
        this.authservices.storeUserData(data['token'], data['user']);
      }
      if(data['user']) {
        
        this.Flashmsg.showFlashMessage({
          messages: ["You logged in "], 
          dismissible: true, 
          timeout: 1000,
          type: 'success', 
        });
        this.router.navigate(['/dashboard'])
      }else {
        this.Flashmsg.showFlashMessage({
          messages: ["Email or Password is wrong!"], 
          dismissible: true, 
          timeout: 2000,
          type: 'danger', 
        });
        this.router.navigate(['/login'])
      }
      
    });
  }
}
